.pagination {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 3.3rem;
    list-style: none;
    font-size: 1.2rem;
    align-items: center;

}

.PaginateNum {
    font-weight: 600;
    text-decoration: none;
    padding: .1rem .3rem;
    margin: 0 0.625rem;
    border-radius: 0.375rem;
    cursor: pointer;
    transition: all 0.3s ease;
    background: transparent;
    color: hsla(160, 14%, 9%, 0.829);
    border: 2px solid hsla(160, 14%, 9%, 0.829);
}

.active {
    font-weight: 900;
    border-color: #101010;
    background: #92ea53;
    color: #101010;
}

@media screen and (max-width: 1100px) {
    @media screen and (max-width: 700px) {
        .pagination {
            margin-top: 2rem;
            font-size: 1rem;
        }

        .PaginateNum {
            padding: .1rem .2rem;
            margin: 0 .2rem;
            border-radius: 0.375rem;
        }
    }
}