.tableMain {
    width: 70%;
}

.coinsTable {
    width: 100%;
}

.tableTitle {
    margin: 1rem 0;
    color: chocolate;
}

.CoinName {
    display: grid;
    gap: .3rem;
    margin: 1rem;
    list-style: none;
}

.CoinImg {
    width: 2.3rem;
}


.CoinImgRow {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
}


.CoinSupply {
    margin: 3rem;
    text-align: end;
    padding-right: 1.8rem;
}

.CoinCap {
    list-style: none;
    align-items: center;
    justify-content: flex-end;
    text-align: end;
    display: flex;
    gap: .5rem;
}

.CoinUp {
    background-color: hsla(155, 100%, 82%, 0.829);
    color: hsl(150, 93%, 28%);
    padding: .5rem .3rem;
    margin: 0 1rem;
    border-radius: .4rem;
    display: flex;
}

.CoinDown {
    background-color: hsl(0, 80%, 88%);
    color: hsl(0, 93%, 47%);
    padding: .5rem .3rem;
    border-radius: .4rem;
    margin: 0 1rem;
    display: flex;
}

.coin24Div {
    position: relative;

}

.Coin24 {
    padding: 0 1rem;
}

.priceIcon {
    font-size: 1.3rem;
    align-self: center;
}

.stickTable {
    position: sticky;
    top: 4rem;
    left: 0;
    color: rgba(51, 69, 86, 0.88);
    background-color: #f1f4f6;
}

@media screen and (max-width: 1100px) {
    .tableMain {
        width: 85%;
        margin: 0 auto;
        font-size: .8rem;
    }

    @media screen and (max-width: 900px) {
        .tableMain {
            width: 95%;
        }

        .removeRowT {
            display: none;
        }

        @media screen and (max-width: 700px) {

            .tableTitle {
                margin: 1rem 1rem 0;
                font-size: 1rem;
            }

            .removeRow {
                display: none;
            }

            .CoinName {
                display: grid;
                gap: .3rem;
                margin: .8rem;
                list-style: none;
            }

            .CoinUp {
                padding: .1rem;
                border-radius: .2rem;
                margin: 0 .4rem;
                align-items: center;
            }

            .CoinDown {
                padding: .1rem;
                border-radius: .2rem;
                margin: 0 .4rem;
                align-items: center;
            }

            .CoinImg {
                width: 2rem;
                padding: 0 .2rem;
            }


            .Coin24 {
                padding: 0 .3rem;
            }

            .stickTable {
                top: 4rem;
            }


        }
    }
}